@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
	background: #fff !important;
	overflow-x: hidden;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	content: "";
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

$color-nobab-light-1: #feffff;
$color-nobab-light-2: #def2f1;
$color-nobab-primary-1: #3aafa9;
$color-nobab-primary-2: #2b7a78;
$color-nobab-dark-2: #17252a;

*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	// font-family: 'Roboto', sans-serif !important;
}

strong {
	font-weight: bold !important;
}

body {
	font-family: "Roboto", sans-serif !important;
}

.block-title {
	border-bottom: 2px solid#eee;

	text-transform: uppercase;

	color: #222;

	font-size: 18px;

	font-weight: 600;

	margin-bottom: 0px;
	line-height: 50px;
	position: relative;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.block-title span::before {
	background: $color-nobab-primary-1;
	content: "";
	display: block;
	position: absolute;
	left: 0;
	right: 0;
	bottom: -2px;
	height: 2px;
	width: 100px;
}

.seeMore-title-box {
	display: flex;
	align-items: center;
	transition: all 0.4s;
	cursor: pointer;
}

.seeMore-title {
	font-weight: normal;
	font-size: 15px;
	top: 5px;
	color: #666;
	text-transform: none;
	font-size: 14px !important;
}

.seeMore-title:hover {
	color: #ff5c00;
}

.seeMore-title-box .fa-chevron-right {
	font-size: 12px;
	color: #666;
	top: 5px;
	margin-left: 3px;
}

@media screen and (max-width: 600px) {
	.block-title {
		line-height: 40px;
	}
}
