$color-nobab-light-1: #feffff;
$color-nobab-light-2: #def2f1;
$color-nobab-primary-1: #3aafa9;
$color-nobab-primary-2: #2b7a78;
$color-nobab-dark-2: #17252a;

.top-head-1 {
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid #eee;
	align-items: center;
	padding: 13px;
	flex-wrap: wrap;
	padding-bottom: 0;
}

.top-head-1 .langandcurrency,
.top-head-1 .trackorderandauthlinks {
	display: flex;
	align-items: center;
	margin-bottom: 13px;
}

.top-head-1 .langandcurrency p,
.top-head-1 .trackorderandauthlinks p {
	padding: 0 10px;
	font-size: 13px;
	font-weight: 500;
	color: #555;
}

.top-head-1 .trackorderandauthlinks p span:hover {
	color: #ff6011;
	cursor: pointer;
}

.top-head-1 .trackorderandauthlinks .trackorderbutton:hover {
	color: #ff6011;
	cursor: pointer;
}

.top-head-1 .trackorderandauthlinks .fa {
	margin: 0 5px;
	color: #555;
}

.top-tags-desc {
	color: #333;
	margin-left: 10px;
}

.tags {
	text-transform: capitalize;
}

@media screen and (max-width: 420px) {
	.top-tags-desc {
		margin-bottom: 10px;
	}
}

.top-tags {
	background: $color-nobab-light-2;
	display: flex;
	padding: 13px;
	align-items: center;
	flex-wrap: wrap;
	font-size: 13px;
	color: #555;
}

.top-tags .tags {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.top-tags .tags h5 {
	padding: 0 15px;
	font-weight: 500;
	line-height: 1.3;
}

.top-tags .tags h5:hover {
	cursor: pointer;
	color: #ff5c32;
}

.image-slider-section {
	padding: 15px 25px;
}

@media screen and (max-width: 765px) {
	.image-slider-section-carousel {
		margin-bottom: 20px;
	}
}

.product-slider-section {
	margin: 15px 25px;
}

.filter-taq-text {
	display: flex;
}

.filter-taq-text h3 {
	padding: 0 15px;
	font-weight: bold;
	color: #555555;
	cursor: pointer;
}

.filter-taq-text h3:hover {
	color: #ff5c32;
}

/* product card style */

.product-card {
	padding: 5px;
	padding: 5px;
	min-width: 250px;
	max-width: 250px;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	border: 1px solid #eeeeee;
	margin-top: 10px;
}

.product-top {
	background: #fff;
	height: 150px;
	width: 150px;
	position: relative;
	border-radius: 5px;
}

@media screen and (max-width: 1308px) {
	// .product-card {
	//   padding: 5px;
	//   min-width: 100%;
	// }

	// .product-top {
	//   background: #fff;
	//   height: 100%;
	//   position: relative;
	//   border-radius: 5px;
	// }
}

.product-top:hover .product-top-overlay {
	display: block;
}

.products-section-title {
	font-size: 21px;
	font-weight: bold;
	color: #333;
	text-transform: uppercase;
}

.product-top-overlay {
	position: absolute; /* Sit on top of the page content */
	display: none; /* Hidden by default */
	width: 100%; /* Full width (cover the whole page) */
	height: 100%; /* Full height (cover the whole page) */
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.2); /* Black background with opacity */
	z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
	cursor: pointer; /* Add a pointer on hover */
	border-radius: 5px;
}

.product-top img {
	width: 100%;
	height: 100%;
	object-fit: contain;
	border-radius: 5px;
}

.overlay-right {
	display: block;
	opacity: 0;
	position: absolute;
	top: 20%;
	margin-left: 0;
	width: 70px;
	z-index: 20;
	left: -62px;
}

.overlay-right .fa {
	cursor: pointer;
	background: #eee;
	color: $color-nobab-dark-2;
	width: 35px;
	height: 35px;
	font-size: 18px;
	padding: 7px;
	margin-top: 5%;
	margin-bottom: 5%;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 3;
}

.overlay-right .btn-secondary {
	background: none !important;
	box-shadow: none !important;
	border: none !important;
}

.product-card:hover .overlay-right {
	opacity: 1;
	margin-left: 5%;
	transition: 0.5s;
}

/* Product bottom css */

.product-bottom {
	padding-top: 10px;
}
.product-bottom .fa {
	color: $color-nobab-primary-1;
	font-size: 12px;
}

.product-top h3 {
	font-size: 20px;
	font-weight: bold;
}

.product-card:hover .product-bottom .cart-btn {
	display: block;
}

.cart-btn {
	margin-bottom: 10px;
	display: none;
}

.product-card:hover .product-bottom .ratingsandtitle {
	display: none;
}

.product-bottom .product-bottom-title {
	font-weight: bold;
	color: $color-nobab-dark-2;
	padding: 10px 0;
	font-size: 16px;
}

.product-bottom-price {
	font-weight: bold;
	color: $color-nobab-primary-1;
}

/* Add To Cart */

.primary-btn {
	border: none;
	outline: none;
	padding: 12px 20px;
	border-radius: 20px;
	background: $color-nobab-dark-2;
	color: #eee;
	text-transform: uppercase;
	font-size: 13px;
	transition: 450ms;
}

.primary-btn:hover {
	background: $color-nobab-primary-1;
}

/*
  Single Product 

*/

.single-product {
	margin-top: 70px;
}

.new-arrival {
	background: green;
	width: 50px;
	color: #fff;
	font-size: 12px;
	font-weight: bold;
	margin-top: 20px;
}

.col-md-7 h2 {
	color: #444444;
	font-size: 19px;
	font-weight: bold;
}

.single-product .fa {
	color: orange;
	font-size: 12px;
}

.rating-review {
	margin-top: 15px;
	display: flex;
	align-items: center;
}

.rating-review p {
	font-size: 13px;
	margin: 0;
	padding: 0;
	margin-left: 7px;
	word-spacing: 2px;
}
.rating-review p span:hover {
	cursor: pointer;
	color: #ff6d7b;
}

.single-product .price {
	color: #ea4c3c;
	font-size: 22px;
	font-weight: bold;
	padding-top: 20px;
}

.single-product input {
	border: 1px solid #ccc;
	font-weight: bold;
	height: 33px;
	text-align: center;
	width: 30px;
}

.single-product .btn .btn-secondary {
	background-color: orange !important;
	color: #fff !important;
	font-size: 15px;
	margin-left: 20px;
	border: none !important;
	box-shadow: none !important;
}

.product-description h6 {
	margin-top: 50px;
	color: orange;
}

.product-description p {
	margin-top: 30px;
}

.product-description hr {
	margin-bottom: 50px;
}

/* quantity counter */

.quantity-counter {
	display: flex;
	align-items: center;
	padding: 5px 15px;
	background: #eeeeee;
}

.quantity-counter p {
	font-size: 12px;
	font-weight: bold;
	color: #555;
	margin-right: 8px;
}

.quantity-counter input {
	border-radius: 2px;
	border: none;
}

.addTocartbtn {
	border: none;
	outline: none;
	padding: 12px 20px;
	border-radius: 5px;
	background: #ff5c00;
	color: #eee;
	text-transform: uppercase;
	font-size: 13px;
	transition: 450ms;
	font-weight: bold;
}

.marginTop25 {
	margin-top: 10px;
}

@media screen and (min-width: 770px) {
	.marginTop25 {
		margin-top: 25px;
	}
}
