$color-nobab-light-1: #feffff;
$color-nobab-light-2: #def2f1;
$color-nobab-primary-1: #3aafa9;
$color-nobab-primary-2: #2b7a78;
$color-nobab-dark-2: #17252a;

.footer {
	padding: 50px 0;
	background: #def2f1;
	margin-top: 100px;
	margin-bottom: -200px;
}

.footer .shopping__info {
	margin-left: 30px;
	margin-top: 30px;
}

.footer .shopping__info .shopping__info-item {
	display: flex;
	padding: 10px 0 10px 32px;
	position: relative;
	margin-bottom: 8px;
	align-items: center;
	font-size: 18px;
}

.footer .shopping__info .shopping__info-item .fa {
	color: #888;
}

.footer .shopping__info .shopping__info-item h3 {
	font-size: 15px;
	color: #888;
	margin-left: 10px;
	line-height: 1.7;
}

.our__services {
	display: flex;
	justify-content: center;
	flex-direction: column;
	// align-items: center;
}

.our__categories {
	padding-left: 60px !important;
}

@media screen and (min-width: 770px) {
	.our__categories {
		margin-top: 30px;
		padding-left: 60px !important;
	}
}

@media screen and (max-width: 600px) {
	.our__categories {
		margin-top: 30px;
		padding-left: 30px !important;
	}
	.shopping__info {
		margin-left: 0 !important;
	}
}

.footer_section-title {
	text-transform: uppercase;

	color: #222;

	font-size: 16px;

	font-weight: 600;

	margin-bottom: 30px;
	line-height: 40px;
	position: relative;
}

.footer_section-title span::before {
	background: $color-nobab-primary-1;
	content: "";
	display: block;
	position: absolute;
	left: 0;
	right: 0;
	bottom: -2px;
	height: 2px;
	width: 40px;
}

.footer_section-item {
	display: flex;
	align-items: center;
	margin-bottom: 15px;
}

.footer_section-item h3 {
	margin-left: 10px;
	font-size: 15px;
	color: #888;
	line-height: 1.7;
}

.footer_section-item i {
	font-size: 15px;
	color: #888;
	line-height: 1.7;
}

.footer_section-item a {
	text-decoration: none;
	margin-left: 10px;
	font-size: 15px;
	color: #888;
	line-height: 1.7;
}

#menu-category-1 .menu {
}

footer .menu .menu-item {
	display: flex;
	padding: 10px 0 10px 5px;
	position: relative;
	margin-bottom: 8px;
	align-items: center;
	font-size: 18px;
}

footer .menu .menu-item a {
	text-decoration: none;
	font-size: 15px;
	color: #666;
	line-height: 1.7;
	margin-top: -10px;
	padding-left: 15px;
	text-transform: capitalize;
	cursor: pointer;
}

footer .menu .menu-item a::before {
	display: inline-block;
	font-style: normal;
	font-variant: normal;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	font-family: FontAwesome;
	font-weight: 900;
	content: "\f111";
	font-size: 15px;
	color: #888;
	line-height: 1.7;
	color: #888;
	position: absolute;
	left: 0;
}

.widget-title {
	display: block;
	text-transform: uppercase !important;

	color: $color-nobab-dark-2 !important;

	font-size: 16px !important;

	font-weight: 600 !important;

	margin-bottom: 30px !important;
	line-height: 40px !important;
	position: relative !important;
}

.widget-title::before {
	background: #3aafa9 !important;
	content: "" !important;
	display: block !important;
	position: absolute !important;
	left: 0 !important;
	right: 0 !important;
	bottom: -2px !important;
	height: 2px !important;
	width: 40px !important;
}
